.statistics {
  width: 450px;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
}

.title {
  margin: 0;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
}

.statList {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style: none;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 90px;
  height: 90px;
  background-color: rgb(233, 233, 233);
  border: 1px solid #d6d6d6;
}

.label {
  font-size: 16px;
  font-weight: 600;
}

.percentage {
  font-weight: bold;
  font-size: 22px;
}
