.transactionHistory {
  width: 600px;
  margin: 0 auto;
  padding-top: 40px;
}

.thead {
  color: #fff;
  background-color: rgb(70, 189, 219);
  height: 40px;
}

.tbody {
  background-color: #fff;
}

.stroke {
  height: 40px;
  text-align: center;
}

.stroke:nth-child(2n) {
  background-color: rgb(212, 212, 212);
}
