.friendList {
  list-style: none;
  margin: 0 auto;
  padding: 40px 0 0 0;
  width: 200px;
}

.item {
  display: flex;
  align-items: center;
  padding: 5px 10px;

  background-color: #fff;
  border-radius: 6px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}

.item:not(:last-child) {
  margin-bottom: 10px;
}

.avatar {
  margin-left: 10px;
  margin-right: 10px;
}

.status {
  display: block;
  width: 10px;
  height: 10px;
  background-color: rgb(101, 28, 170);
  border-radius: 90%;
}

.true {
  composes: status;
  background-color: rgb(33, 153, 133);
}

.false {
  composes: status;
  background-color: rgb(255, 0, 0);
}
