.profile {
  margin: 0 auto;
  width: 270px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  margin-bottom: 40px;
  border-radius: 6px;
}

.description {
  padding-top: 40px;
  padding-bottom: 40px;
  text-align: center;
}

.avatar {
  width: 100px;
  border-radius: 12px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  background-color: rgb(123, 139, 209);
}

.name {
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 22px;
}

.tag,
.location {
  font-size: 18px;
  font-weight: 600;
  color: rgb(128, 128, 128);
}

.tag {
  margin-bottom: 10px;
}

.stats {
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
  list-style: none;
}

.stats li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 90px;
  height: 90px;

  background-color: rgb(233, 233, 233);
  border: 1px solid #d6d6d6;
}

.label {
  font-size: 16px;
  font-weight: 600;
  color: rgb(128, 128, 128);
}

.quantity {
  font-weight: bold;
  font-size: 20px;
}
